export default{
	name:'NavControl',
	props:{
		navItems:{
			type:Array,
			default(){
				return ['全部','待付款','配送中','租贷中','已完成'];
			}
		}
	},
	data(){
		return {
			currentIndex:0
		}
	},
	methods:{
		itemClick(index){
			this.currentIndex = index
			this.$emit('itemClick',index)
		}
	}
}
